@import url(https://fonts.googleapis.com/css?family=Comfortaa);
:root {
  --textColor: #212121;
  --primaryWhiteColor: #ffffff;
  --bgColor: #d8bfd8;
  --darkVioletColor: #391d35;
  --violetColor: #613d5c;
  --shadow-dark: gray 1px 1px 10px, gray -1px -1px 10px, gray -1px 1px 10px,
    gray 1px -1px 10px;
  --transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  background-image: url(/goit-react-hw-02-feedback/static/media/seruzor68.057651ab.jpg);
  font-family: 'Comfortaa', cursive;

  font-weight: 500;
  font-size: 12px;
  line-height: 1.33;
}

a {
  text-decoration: none;
  outline: none;
}

ul {
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
p {
  margin: 0;
}

h1,
h2,
h3,
button {
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

li {
  list-style: none;
}

li:before {
  content: '★';
  margin-right: 8px;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.Container_container__2Vb3E {
  margin-top: 100px;
  margin-left: auto;
  margin-right: auto;
  padding: 30px;
  /* padding-left: 15px;
  padding-right: 15px; */

  width: 500px;
  background-color: var(--bgColor);
  border-radius: 10px;
  box-shadow: var(--shadow-dark);
}

.Statistics_statistics__37GYF {
  margin-top: 10px;
  text-align: left;
}

.Statistics_item__2VN0C {
  font-size: 15px;
  font-weight: 700;
  color: var(--textColor);
}

.FeedbackOptions_button__1WLCL {
  display: inline-block;
  margin-top: 30px;
  padding: 10px 20px;

  color: var(--primaryWhiteColor);
  background-color: grey;

  font-size: 13px;
  font-weight: 700;
  text-transform: uppercase;

  outline: none;
  border-radius: 10px;
  border: none;
  border: solid var(--primaryWhiteColor) 2px;

  cursor: pointer;
  transition: var(--transition);
}

.FeedbackOptions_button__1WLCL:not(:last-child) {
  margin-right: 20px;
}

.FeedbackOptions_button__1WLCL:hover {
  background-color: var(--darkVioletColor);
  transform: scale(1.1);
}

.Section_section__2xEZl {
  text-align: center;
  margin-left: auto;
  margin-right: auto;
}

.Section_section__2xEZl:not(:first-child) {
  margin-top: 30px;
}

.Section_title__1DCZx {
  font-weight: 700;
  font-size: 25px;
  color: var(--violetColor);
}

.Notification_message__16V6n {
  color: grey;
}

